* {
    box-sizing: border-box;
  }
  
  .dropdownl {
    float: left;
    overflow: hidden;
  }
  
  .dropdownl{
    font-size: 16px;  
    border: none;
    outline: none;
    color: black;
    background-color: white;
    font: inherit;
    margin: 0;
  }
  
  .dropdownl-content {
    display: none;
    position: relative;
    background-color: white;
    z-index: 100000;
    width: 500px;
    padding:10px;
    box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
  }
  
  .dropdownl:hover .dropdownl-content {
    display: block;
  }
  .dropdownl:hover p {
    color: gray;
    cursor: pointer;
  }
  
  
  /* Create three equal columns that floats next to each other */
  .column {
    float: left;
    width: 33.33%;
    padding: 10px;
    background-color: white;
    height: 250px;
  }
  
  .column a {
    float: none;
    color: black;
    padding: 16px;
    text-decoration: none;
    display: block;
    text-align: left;
  }
  
  .column a:hover {
    background-color: gray;
  }
  
  /* Clear floats after the columns */
  .row:after {
    content: "";
    display: table;
    clear: both;
  }
  
  @media screen and (max-width: 600px) {
    .column {
      width: 100%;
      height: auto;
    }
  }  